import React from "react";
import { useEffect } from "react";
import { useRef } from "react";
import { useLocation } from "react-router-dom";
import Carousel from "../home/Carousel";
import { Philosopy } from "./Philosophy";

const AboutPage = () => {
  const ref = useRef(null);
  let location = useLocation();
  let scroll = Boolean(
    new URLSearchParams(location.search).get("naturalFabric")
  );
  useEffect(() => {
    if (scroll) {
      ref.current.scrollIntoView();
    }
  }, []);

  return (
    <>
      <Carousel />
      <Philosopy />
      <div className="wrapper custom-wrapper">
        <div class="wrapper-item-about fabrics">
          <div class="fabric-section-one">
            <h1 className="font-Futura-medium text-capitalize" ref={ref}>
              Our Fabrics
            </h1>
            <p className="fabrics-p">
              At Nadaasi, we are committed to provide the highest quality
              dresses made of natural fabrics, trendy timeless designs and
              fashionable look to make you feel the beauty of standing out in a
              crowd and be the centre of attention
            </p>
          </div>
          <div class="fabric-section fabric-section-two">
            <img
              src="data:image/png;base64,"
              alt="Chiffon"
            />
            <h1>Chiffon/ Silk Chiffon</h1>
            <p className="fabrics-p ">
              Silk Chiffon Fabric remains a dignified, sheer fabric with an ease
              in drape and crepe-like texture. The inherent brilliance of this
              fabric is due to the triangular shape of each fibre that refracts
              light in many directions. Lightweight and absorbent, Chiffon
              Fabric will keep you comfortable in the summer as well as warm in
              the winter since it holds warm air close to your skin.
            </p>
          </div>
          <div class="fabric-section fabric-section-three">
            <img
              src="data:image/png;base64,"
              alt="Geogette"
            />
            <h1>Georgette</h1>
            <p className="fabrics-p">
              It was named after the early 20th century French dressmaker
              Georgette de la Plante. Georgette is originally made of silk which
              is a sheer, lightweight, dull-finished crpe fabric. Its crinkly
              crepe-light texture makes it one of the most distinctive fabrics.
            </p>
          </div>
          <div class="fabric-section fabric-section-four">
            <img
              src="data:image/png;base64,"
              alt="PureCotton"
            />
            <h1>Pure Cotton</h1>
            <p className="fabrics-p">
              Cotton is the world's most widely used natural fibre and still the
              undisputed "king" of the global textiles industry. Cotton is
              almost pure cellulose, with softness and breathability that have
              made it the world's most popular natural fibre.
            </p>
          </div>
          <div class="fabric-section fabric-section-five">
            <img
              src="data:image/png;base64,"
              alt="Silk"
            />
            <h1>Silk Organza</h1>
            <p className="fabrics-p">
              Silk Organza Fabric is naturally thin, sheer, lightweight and
              crisp woven from lustrous silk yarns. Organza Fabric has an
              incredible sheen without any stiffness.
            </p>
          </div>
        </div>
      </div>
    </>
  );
};

export default AboutPage;
